html,
body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
}

h1 {
  font-weight: normal;
}

.App {
  max-width: 1200px;
  margin: 20px auto;
}

.App::before {
  content: '';
  display: block;
  height: 100vh;
  width: 40px;
  position: fixed;
  top: 0;
  left: 0;
  background-color: #009fb7;
}

.logout {
  position: absolute;
  top: 30px;
  right: 50px;
  font-size: 16px;
}
