.Page-layout {
  margin-top: 50px;
  margin-left: 40px;
}

.Unit-body {
  margin-left: 80px;
}
.Unit-boxes {
  text-align: left;
}

.ReturnArrow {
  position: relative;
  background: none !important;
  padding: 0 !important;
  font-family: 'Open Sans', sans-serif;
  border: none;
  color: rgb(0, 159, 183);
  text-decoration: none;
  margin-left: 0;
  cursor: pointer;
  font-size: inherit;
}

.Unit-header {
  font-family: 'Open Sans', sans-serif;
  font-weight: lighter;
  font-size: 36px;
  position: relative;
  border: none;
}

.addButton {
  text-align: left;
  padding-left: 30px;
  text-decoration: none;
  margin-right: 1000px;
  margin-bottom: 20px;
  color: black;
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
  background-color: #fed766;
  width: 200px;
  float: left;
  padding: 18px;
  border-radius: 2px;
  box-shadow: none;
  border: 0;
}
.addButton:hover {
  background-color: #fdca21dc;
}
.addButton:active {
  text-decoration: none;
  border: 0;
}
.addButton:focus {
  outline: none;
}

.Expand {
  height: 350px;
}

.newUnitModal {
  box-shadow: 5px 5px 100px grey;
  font-family: 'Open Sans', sans-serif;
  font-weight: lighter;
  font-size: 16px;
  margin: 150px auto;
  padding-top: 40px;
  padding-left: 40px;
  padding-right: 40px;
  padding-bottom: 50px;
  background-color: white;
  height: auto;
  width: 400px;
  border-top: 45px solid #009fb7;
}

.modalTitle {
  font-family: 'Open Sans', sans-serif;
  font-size: 26px;
  padding-bottom: 6%;
}

.buttonwrapper {
  text-align: center;
}

.cancelButton {
  font-family: 'Open Sans', sans-serif;
  border-radius: 4px;
  border-style: solid;
  text-align: center;
  text-decoration: none;
  color: black;
  margin: 15% 7.5% 0 7.5%;
  font-size: 16px;
  background-color: #fed766;
  width: 80px;
  padding: 10px;
  box-shadow: none;
  border: 0;
}
.cancelButton:hover {
  background-color: #fdca21dc;
}

.inputText {
  background-color: #f4f4f4;
  font-family: 'Open Sans', sans-serif;
  font-size: 15px;
  border: solid;
  padding: 10px;
  width: 90%;
  height: 20px;
  margin-top: 10px;
  border-color: #ebe9e9;
}

.inputText:focus {
  outline: none;
}

.newUnitModal:focus {
  outline: none;
}

.shiftRight {
  margin-left: 80px;
  margin-top: 60px;
}

.question-wrapper {
  height: 230px;
  overflow: auto;
  border-bottom: solid 0.75px #ebe9e9;
}

.addButtonWrapper {
  text-align: center;
}

.center {
  margin: 30px auto;
  width: auto;
}

::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background: #888;
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}
